.page--home {
  max-width: 1690px;
  min-width: 1112px;
}
.page--home h3 {
  border-bottom: 1px solid #d8dde6;
  padding-bottom: 8px;
}
.page--home h3 .pui-icon {
  -webkit-transition: all 0.2s ease-in;
  -o-transition: all 0.2s ease-in;
  transition: all 0.2s ease-in;
  margin-left: 0;
  opacity: 0;
}
.page--home h3 a:hover .pui-icon {
  margin-left: 8px;
  opacity: 1;
}
.page--home section {
  margin-bottom: 48px;
}
.page--home__thumbs {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: start;
      align-items: flex-start;
  -ms-flex-pack: start;
      justify-content: flex-start;
  -ms-flex-direction: row;
      flex-direction: row;
}
.page--home .pui-thumbnail-list-item {
  display: block;
  margin-right: 16px;
}
.page--home .pui-thumbnail-list-item__badge span {
  color: #ffffff;
}
.page--home .pui-thumbnail-list-item:last-of-type {
  margin-right: 0;
}
.page--home__stats-block {
  margin-right: 32px;
}
.page--home__stats-block h2 {
  margin-bottom: 0;
}
.page--home__stats-block div {
  font-weight: 600;
  margin-bottom: 16px;
}
.page--home__links {
  max-width: 260px;
  margin-left: 16px;
}
@media screen and (max-width: 1900px) {
  .page--home {
    max-width: 1412px;
  }
  .page--home .pui-thumbnail-list-item:nth-of-type(6) {
    display: none;
  }
}
@media screen and (max-width: 1600px) {
  .page--home .pui-thumbnail-list-item:nth-of-type(5) {
    display: none;
  }
}



